.pageNumber{
    font-weight: 600;
    font-size: 18px;
    color: indianred;
    margin: 0 10px;
    cursor: pointer;
}
.activePageNumber{
    font-weight: 700;
    color: #9370d8;
    font-size: 22px;
    cursor: pointer;
    margin: 0 11px;
}
.paginatorButton{
    display: inline-block;
    margin: 0 10px;
    padding: 10px 15px;
    border-radius: 35%;
    font-weight: 800;
}
.paginator{
    margin-bottom: 20px;
}